<template>
  <div>
    <div class="logo">
      <div class="container">
        <a href="#"></a>
      </div>
    </div>

    <div class="panel">
      <div class="container">
        <div class="panel-layout">
          <div class="panel-tabs">
            <a
              href="javascript:void(0);"
              class="tabs-link"
              data-tab="login"
              :class="{select:isLogin}"
              @click="changeTab(true)"
            >账号登录</a>
            <span class="line"></span>
            <a
              href="javascript:void(0);"
              class="tabs-link"
              data-tab="register"
              :class="{select:!isLogin}"
              @click="changeTab(false)"
            >账号注册</a>
          </div>
          <div class="panel-account">
            <form action="javascript:;" method="POST" id="form">
              <!--action:javascript:;阻止默认操作-刷新-->
              <label class="account-label" for="username">
                <input
                  class="account-item"
                  autocomplete="off"
                  type="text"
                  name="name"
                  id="username"
                  placeholder="账号"
                  v-model="form.name"
                  v-validate="'required'"
                  data-vv-as="用户名"
                >
              </label>
              <!-- <div class="error">{{ errors.first('name')}}</div> -->
               <div class="error" v-if="errors.has('name')">请输入正确的用户名</div>
              <label class="account-label" for="pwd">
                <input
                  class="account-item"
                  type="password"
                  placeholder="密码"
                  autocomplete="off"
                  id="pwd"
                  name="password"
                  v-model="form.password"
                  v-validate="'required'"
                  data-vv-as="密码"
                  ref="password"
                >
              </label>
              <!-- <div class="error">{{ errors.first('password')}}</div> -->
               <div class="error" v-if="errors.has('password')">请输入正确的密码</div>
              <label class="account-label password2" for="pwd2" id="regsterLabel" v-if="!isLogin">
                <input
                  class="account-item"
                  type="password"
                  placeholder="重复密码"
                  autocomplete="off"
                  id="pwd2"
                  name="password2"
                  v-model="form.password2"
                  v-validate="'required|confirmed:password'"
                     data-vv-as="重复密码"
                >
              </label>
               <div class="error" v-if="errors.has('password2')">请输入正确的重复密码</div>
              <input
                class="btn"
                id="btn"
                type="submit"
                :value="isLogin ? '登录':'注册'"
                @click="submit"
              >
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  mounted() {
    if (this.$route.query.type === "register") {
      this.isLogin = false;
    }
  },
  data() {
    return {
      isLogin: true,
      form: {
        name: "",
        password: "",
        password2: ""
      }
    };
  },
  methods: {
    ...mapActions("user", ["login", "register"]),
    submit() {
      this.$validator.validateAll().then(val => {
        if (val) {
          if (this.isLogin) {
            //注册
            this.login(this.form).then(() => {
              this.$router.push("/");
            });
          } else {
            //登录
            this.register(this.form).then(() => {
              this.$router.push("/");
            });
          }
        }
      });
    },
    changeTab(flag){
        this.isLogin=flag;
        this.form={};
        this.$validator.reset();//错误提示重置
    }
  }
};
</script>

<style  scoped>
body,
a {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}
.error {
  color: #ef5b00;
  margin-bottom: 10px;
}
.container {
  width: 1130px;
  margin: 0 auto;
}

.container::before,
.container::after,
.clearfix::before,
.clearfix::after {
  content: "";
  display: table;
}

.container::after,
.clearfix::after {
  clear: both;
}

.logo a {
  display: block;
  background: url(../img/mistore_logo.png) no-repeat;
  width: 200px;
  height: 98px;
}

.panel {
  background: url(../img/loginbg.jpg) no-repeat top center;
  height: 588px;
}

.panel-layout {
  width: 410px;
  min-height: 524px;
  background: white;
  margin-top: 32px;
  float: right;
}

.panel-tabs {
  padding: 27px 0 24px;
  text-align: center;
  font-size: 24px;
  color: #e0e0e0;
}

.panel-tabs .select {
  color: #f56600;
}

.panel-tabs .line {
  width: 1px;
  height: 24px;
  margin: 0 35px 0 42px;
  border: 1px solid #e0e0e0;
}

.tabs-link {
  padding: 27px 0 24px;
  text-align: center;
  font-size: 24px;
  color: #666;
}

.panel-account {
  width: 348px;
  margin: 0 auto;
  margin-top: 10px;
}

.account-item {
  width: 306px;
  height: 22px;
  line-height: 22px;
  padding: 13px 16px 13px 14px;
  display: block;
  outline: 0;
  border: 0 none;
  color: #333;
}

.account-label {
  margin-bottom: 14px;
  border: 1px solid #e0e0e0;
  outline: 0 none;
  display: -webkit-box;
}

.panel-account .btn {
  background-color: #ef5b00;
  width: 100%;
  height: 50px;
  line-height: 50px;
  display: block;
  margin-bottom: 14px;
  text-align: center;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  border: 0;
  outline: 0;
}
</style>
